	          show : true,
	          feature : {
	            mark : {show: true},
	            dataView : {show: true, readOnly: false},
	            magicType: {show: true, type: ['line', 'bar']},
	            restore : {show: true},
	            saveAsImage : {show: true}
	          }
	        },
	        grid: {
	          top: '12%',
	          left: '5%',
	          right: '5%',
	          containLabel: true
	        },
	        legend: {
	          data:['Growth', 'Result'],
	          itemGap: 5,
